<template>
  <h1>Reaction Timer</h1>
  <button @click="start" :disabled="isPlay">Play</button>
  <Block v-if="isPlay" :delay="delay" @closeing="end" />
  <Result v-if="done">
    <h5>Tour Reaction is - {{ time }} ms</h5>
  </Result>
</template>

<script>
import Block from "./components/Block.vue";
import Result from "./components/Result.vue";
export default {
  name: "App",
  components: { Block, Result },
  data() {
    return {
      delay: null,
      isPlay: false,
      done: false,
      time: null,
    };
  },
  methods: {
    start() {
      this.isPlay = true;
      this.delay = 2000 + Math.random() * 5000;
      this.done = false;
    },
    end(Timer) {
      this.isPlay = false;
      this.done = true;
      this.time = Timer;
    },
  },
};
</script>

