<template>
  <div class="op" v-if="show">
    <div class="mode" @click="close">
      <p>click her</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      first: null,
      secound: null,
      time: null,
    };
  },
  props: ["delay"],
  methods: {
    close() {
      this.secound = new Date();
      this.time = this.secound - this.first;
      this.$emit("closeing", this.time);
    },
  },
  mounted() {
    setTimeout(() => {
      this.show = true;
      this.first = new Date();
    }, this.delay);
  },
  unmounted() {},
};
</script>

<style>
.mode {
  max-width: 400px;
  padding: 50px;
  margin: auto;
  margin-top: 120px;
  color: white;
  background-color: green;
  display: flex;
  justify-content: center;
  align-items: center;
}
.op {
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.5;
  position: fixed;
  top: 0;
}
</style>
